:root {
    --hanyang_blue: #0E4A84;
}
input, button, select, optgroup, textarea {
    /* margin: 0; */
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

*, *::before, *::after {
    box-sizing: border-box;
}

button:not(:disabled),
[type=button]:not(:disabled),
[type=reset]:not(:disabled),
[type=submit]:not(:disabled) {
    cursor: pointer;
}

.formTitle {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    margin: 5rem 0;
}

.title {
    font-size: 2rem;
    font-weight: bold;
}

.logo {
    width: 3rem;
    margin-right: 3px;
}

.submit_btn:disabled {
    pointer-events: none;
    opacity: 0.38;
}


.submit_btn {
    color: white;
    font-weight: 600;
    width: 100%;
    padding: 0.75rem 0.375rem;
    margin-top: 2rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.375rem;
    background-color: black;
    border: none;
}

.container {
    margin: 0 9rem;
}


.inputLabel {
    margin-bottom: 0.35rem;
    display: inline-block;
}

.inputForm {

    display: block;
    width: 100%;
    padding: 0.575rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    border: 1px solid #ced4da;
    appearance: none;
    border-radius: 0.375rem;
    transition: border-color 0.1s ease-in-out, box-shadow 0.15s ease-in-out;
}

.inputForm:focus {
    outline: 2px solid black;
}

.field {

}

.MutedText {
    opacity: 1;
    color: black;
    margin-top: 0.25rem;
    font-size: 0.82rem;
}

.EssentialInput {
    opacity: 0.7;
    color: red;
    margin-top: 0.25rem;
    font-size: 0.82rem;
}

.switch_label {
    font-size: 1rem;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.switch_button {
    margin-left: auto;
}

.inputContainer {
    position: relative;
    display: flex;
    flex-direction: column;
}

.floatLabel {
    display: flex;
    flex-direction: column;
    min-width: 350px;
    margin-bottom: 2rem;
}

.floatLabel input {
    width: 100%;
    height: 50px;
    padding: 14px 16px 0 10px;
    outline: 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    background: #fff;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
}

.floatLabel label {
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0 12px;
    color: #999;
    pointer-events: none;
    position: absolute;
    transform: translate(0,14px) scale(1);
    transform-origin: top left;
    transition: all 0.2s ease-out;
}

.floatLabel:focus-within label {
    transform: translate(0,5px) scale(0.7);
}

.floatLabel .Active {
    transform: translate(0, 5px) scale(0.7);
}

[type="checkbox"] {
    appearance: none;
    position: relative;
    border: max(2px, 0.1em) solid gray;
    border-radius: 1.25em;
    width: 2.3em;
    height: 1.25em;
}

[type="checkbox"]:enabled:hover {
    box-shadow: 0 0 0 max(3px, 0.1em) lightgray;
                 }

[type="checkbox"]::before {
    content: "";
    position: absolute;
    left: 0;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    transform: scale(0.8);
    background-color: gray;
    transition: left 150ms linear;
}

[type="checkbox"]:checked::before {
    background-color: white;
    left: 1.1em;
}

[type="checkbox"]:checked {
    background-color: black;
    border-color: black;
}

